html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
html #root,
body #root {
  width: 100vw;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
}
html #root .camera,
body #root .camera {
  position: relative;
  width: 100%;
  height: 100%;
}
html #root .camera video,
body #root .camera video {
  width: 100%;
  position: absolute;
  z-index: 0;
}
html #root .camera .cameraFeed,
body #root .camera .cameraFeed {
  width: 100%;
  z-index: 1;
  position: absolute;
}
html #root .camera .result,
body #root .camera .result {
  z-index: 2;
  position: absolute;
  background: transparent;
  display: none;
}
html #root .camera .result.hasPhoto,
body #root .camera .result.hasPhoto {
  display: block;
  background-color: #fff;
}
html #root .camera .result canvas,
body #root .camera .result canvas {
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
html #root .camera video.front,
body #root .camera video.front {
  width: 30%;
  z-index: 1;
  top: 0.5em;
  right: 0.5em;
  -webkit-transform: scaleX(-1);
  transform: scaleX(-1);
}
html #root .camera .controls,
body #root .camera .controls {
  position: absolute;
  z-index: 1;
  bottom: 0;
  margin: 0 auto;
}
html #root button,
body #root button,
html #root select,
body #root select,
html #root .download,
body #root .download {
  outline: 0;
  border: 0;
  padding: 1em;
  margin-bottom: 1em;
  background-color: #eee;
  font-size: 0.7em;
  width: 100%;
  text-align: center;
}
html #root .download,
body #root .download {
  display: block;
}
html #root .download.isRecording,
body #root .download.isRecording {
  display: none;
}
